package strings

import io.github.skeptick.libres.strings.PluralForms
import io.github.skeptick.libres.strings.VoidFormattedString
import io.github.skeptick.libres.strings.VoidPluralString
import io.github.skeptick.libres.strings.getCurrentLanguageCode
import kotlin.String

public object StringsFi : Strings {
  override val byte_symbol: String = "t"

  override val kilobyte_symbol: String = "kt"

  override val megabyte_symbol: String = "Mt"

  override val gigabyte_symbol: String = "Gt"

  override val terabyte_symbol: String = "Tt"

  override val groupSeparator: String = ""

  override val decimalSymbol: String = ","

  override val time_ago: VoidFormattedString = VoidFormattedString("%1${'$'}s sitten")

  override val time_in_future: VoidFormattedString =
      VoidFormattedString("%1${'$'}s tulevaisuudessa")

  override val now: String = "nyt"

  override val seconds: VoidPluralString = VoidPluralString(PluralForms(one = "sekunti", other =
      "sekuntia", ), "fi")

  override val seconds_future: VoidPluralString? = null

  override val seconds_past: VoidPluralString? = null

  override val minutes: VoidPluralString = VoidPluralString(PluralForms(one = "minuutti", other =
      "minuuttia", ), "fi")

  override val minutes_future: VoidPluralString? = null

  override val minutes_past: VoidPluralString? = null

  override val hours: VoidPluralString = VoidPluralString(PluralForms(one = "tunti", other =
      "tuntia", ), "fi")

  override val hours_future: VoidPluralString? = null

  override val hours_past: VoidPluralString? = null

  override val days: VoidPluralString = VoidPluralString(PluralForms(one = "päivä", other =
      "päivää", ), "fi")

  override val days_future: VoidPluralString? = null

  override val days_past: VoidPluralString? = null

  override val weeks: VoidPluralString = VoidPluralString(PluralForms(one = "viikko", other =
      "viikkoa", ), "fi")

  override val weeks_future: VoidPluralString? = null

  override val weeks_past: VoidPluralString? = null

  override val months: VoidPluralString = VoidPluralString(PluralForms(one = "kuukausi", other =
      "kuukautta", ), "fi")

  override val months_future: VoidPluralString? = null

  override val months_past: VoidPluralString? = null

  override val years: VoidPluralString = VoidPluralString(PluralForms(one = "vuosi", other =
      "vuotta", ), "fi")

  override val years_future: VoidPluralString? = null

  override val years_past: VoidPluralString? = null
}
