package strings

import io.github.skeptick.libres.strings.PluralForms
import io.github.skeptick.libres.strings.VoidFormattedString
import io.github.skeptick.libres.strings.VoidPluralString
import io.github.skeptick.libres.strings.getCurrentLanguageCode
import kotlin.String

public object StringsFr : Strings {
  override val byte_symbol: String = "o"

  override val kilobyte_symbol: String = "ko"

  override val megabyte_symbol: String = "Mo"

  override val gigabyte_symbol: String = "Go"

  override val terabyte_symbol: String = "To"

  override val groupSeparator: String = ""

  override val decimalSymbol: String = "."

  override val time_ago: VoidFormattedString = VoidFormattedString("il y a %1${'$'}s")

  override val time_in_future: VoidFormattedString = VoidFormattedString("dans %1${'$'}s")

  override val now: String = "maintenant"

  override val seconds: VoidPluralString = VoidPluralString(PluralForms(one = "seconde", other =
      "secondes", ), "fr")

  override val seconds_future: VoidPluralString? = null

  override val seconds_past: VoidPluralString? = null

  override val minutes: VoidPluralString = VoidPluralString(PluralForms(one = "minute", other =
      "minutes", ), "fr")

  override val minutes_future: VoidPluralString? = null

  override val minutes_past: VoidPluralString? = null

  override val hours: VoidPluralString = VoidPluralString(PluralForms(one = "heure", other =
      "heures", ), "fr")

  override val hours_future: VoidPluralString? = null

  override val hours_past: VoidPluralString? = null

  override val days: VoidPluralString = VoidPluralString(PluralForms(one = "jour", other = "jours",
      ), "fr")

  override val days_future: VoidPluralString? = null

  override val days_past: VoidPluralString? = null

  override val weeks: VoidPluralString = VoidPluralString(PluralForms(one = "semaine", other =
      "semaines", ), "fr")

  override val weeks_future: VoidPluralString? = null

  override val weeks_past: VoidPluralString? = null

  override val months: VoidPluralString = VoidPluralString(PluralForms(one = "mois", other = "mois",
      ), "fr")

  override val months_future: VoidPluralString? = null

  override val months_past: VoidPluralString? = null

  override val years: VoidPluralString = VoidPluralString(PluralForms(one = "an", other = "ans", ),
      "fr")

  override val years_future: VoidPluralString? = null

  override val years_past: VoidPluralString? = null
}
