package strings

import io.github.skeptick.libres.strings.PluralForms
import io.github.skeptick.libres.strings.VoidFormattedString
import io.github.skeptick.libres.strings.VoidPluralString
import io.github.skeptick.libres.strings.getCurrentLanguageCode
import kotlin.String

public object StringsDe : Strings {
  override val byte_symbol: String? = null

  override val kilobyte_symbol: String? = null

  override val megabyte_symbol: String? = null

  override val gigabyte_symbol: String? = null

  override val terabyte_symbol: String? = null

  override val groupSeparator: String = "."

  override val decimalSymbol: String = ","

  override val time_ago: VoidFormattedString = VoidFormattedString("vor %1${'$'}s")

  override val time_in_future: VoidFormattedString = VoidFormattedString("in %1${'$'}s")

  override val now: String = "jetzt"

  override val seconds: VoidPluralString = VoidPluralString(PluralForms(one = "Sekunde", other =
      "Sekunden", ), "de")

  override val seconds_future: VoidPluralString? = null

  override val seconds_past: VoidPluralString? = null

  override val minutes: VoidPluralString = VoidPluralString(PluralForms(one = "Minute", other =
      "Minuten", ), "de")

  override val minutes_future: VoidPluralString? = null

  override val minutes_past: VoidPluralString? = null

  override val hours: VoidPluralString = VoidPluralString(PluralForms(one = "Stunde", other =
      "Stunden", ), "de")

  override val hours_future: VoidPluralString? = null

  override val hours_past: VoidPluralString? = null

  override val days: VoidPluralString = VoidPluralString(PluralForms(one = "Tag", other = "Tage", ),
      "de")

  override val days_future: VoidPluralString = VoidPluralString(PluralForms(other = "Tagen", ),
      "de")

  override val days_past: VoidPluralString = VoidPluralString(PluralForms(other = "Tagen", ), "de")

  override val weeks: VoidPluralString = VoidPluralString(PluralForms(one = "Woche", other =
      "Wochen", ), "de")

  override val weeks_future: VoidPluralString? = null

  override val weeks_past: VoidPluralString? = null

  override val months: VoidPluralString = VoidPluralString(PluralForms(one = "Monat", other =
      "Monate", ), "de")

  override val months_future: VoidPluralString = VoidPluralString(PluralForms(other = "Monaten", ),
      "de")

  override val months_past: VoidPluralString = VoidPluralString(PluralForms(other = "Monaten", ),
      "de")

  override val years: VoidPluralString = VoidPluralString(PluralForms(one = "Jahr", other = "Jahre",
      ), "de")

  override val years_future: VoidPluralString = VoidPluralString(PluralForms(other = "Jahren", ),
      "de")

  override val years_past: VoidPluralString = VoidPluralString(PluralForms(other = "Jahren", ),
      "de")
}
