  override val seconds_future: VoidPluralString? = null

  override val seconds_past: VoidPluralString = VoidPluralString(PluralForms(one = "sekundou", few =
      "sekundami", other = "sekundami", ), "cs")

  override val minutes: VoidPluralString = VoidPluralString(PluralForms(one = "minuta", few =
      "minuty", other = "minut", ), "cs")

  override val minutes_future: VoidPluralString? = null

  override val minutes_past: VoidPluralString = VoidPluralString(PluralForms(one = "minutou", few =
      "minutami", other = "minutami", ), "cs")

  override val hours: VoidPluralString = VoidPluralString(PluralForms(one = "hodina", few =
      "hodiny", other = "hodin", ), "cs")

  override val hours_future: VoidPluralString? = null

  override val hours_past: VoidPluralString = VoidPluralString(PluralForms(one = "hodinou", few =
      "hodinami", other = "hodinami", ), "cs")

  override val days: VoidPluralString = VoidPluralString(PluralForms(one = "den", few = "dny", other
      = "dní", ), "cs")

  override val days_future: VoidPluralString? = null

  override val days_past: VoidPluralString = VoidPluralString(PluralForms(one = "dnem", few = "dny",
      other = "dny", ), "cs")

  override val weeks: VoidPluralString = VoidPluralString(PluralForms(one = "týden", few = "týdny",
      other = "týdnů", ), "cs")

  override val weeks_future: VoidPluralString? = null

  override val weeks_past: VoidPluralString = VoidPluralString(PluralForms(one = "týdnem", few =
      "týdny", other = "týdny", ), "cs")

  override val months: VoidPluralString = VoidPluralString(PluralForms(one = "měsíc", few =
      "měsíce", other = "měsíců", ), "cs")

  override val months_future: VoidPluralString? = null

  override val months_past: VoidPluralString = VoidPluralString(PluralForms(one = "měsícem", few =
      "měsíci", other = "měsíci", ), "cs")

  override val years: VoidPluralString = VoidPluralString(PluralForms(one = "rok", few = "roky",
      other = "let", ), "cs")

  override val years_future: VoidPluralString? = null

  override val years_past: VoidPluralString = VoidPluralString(PluralForms(one = "rokem", few =
      "roky", other = "lety", ), "cs")
