  override val months_future: VoidPluralString = VoidPluralString(PluralForms(one = "айдан", other =
      "айдан", ), "kk")

  override val months_past: VoidPluralString? = null

  override val years: VoidPluralString = VoidPluralString(PluralForms(one = "жыл", other = "жыл", ),
      "kk")

  override val years_future: VoidPluralString = VoidPluralString(PluralForms(one = "жылдан", other =
      "жылдан", ), "kk")

  override val years_past: VoidPluralString? = null
