package strings

import io.github.skeptick.libres.strings.PluralForms
import io.github.skeptick.libres.strings.VoidFormattedString
import io.github.skeptick.libres.strings.VoidPluralString
import io.github.skeptick.libres.strings.getCurrentLanguageCode
import kotlin.String

public object StringsRu : Strings {
  override val byte_symbol: String? = null

  override val kilobyte_symbol: String? = null

  override val megabyte_symbol: String? = null

  override val gigabyte_symbol: String? = null

  override val terabyte_symbol: String? = null

  override val groupSeparator: String = ""

  override val decimalSymbol: String = ","

  override val time_ago: VoidFormattedString = VoidFormattedString("%1${'$'}s назад")

  override val time_in_future: VoidFormattedString = VoidFormattedString("через %1${'$'}s")

  override val now: String = "сейчас"

  override val seconds: VoidPluralString = VoidPluralString(PluralForms(one = "секунда", few =
      "секунды", many = "секунд", ), "ru")

  override val seconds_future: VoidPluralString = VoidPluralString(PluralForms(one = "секунду", ),
      "ru")

  override val seconds_past: VoidPluralString = VoidPluralString(PluralForms(one = "секунду", ),
      "ru")

  override val minutes: VoidPluralString = VoidPluralString(PluralForms(one = "минута", few =
      "минуты", many = "минут", ), "ru")

  override val minutes_future: VoidPluralString = VoidPluralString(PluralForms(one = "минуту", ),
      "ru")

  override val minutes_past: VoidPluralString = VoidPluralString(PluralForms(one = "минуту", ),
      "ru")

  override val hours: VoidPluralString = VoidPluralString(PluralForms(one = "час", few = "часа",
      many = "часов", ), "ru")

  override val hours_future: VoidPluralString? = null

  override val hours_past: VoidPluralString? = null

  override val days: VoidPluralString = VoidPluralString(PluralForms(one = "день", few = "дня", many
      = "дней", ), "ru")

  override val days_future: VoidPluralString? = null

  override val days_past: VoidPluralString? = null

  override val weeks: VoidPluralString = VoidPluralString(PluralForms(one = "неделя", few =
      "недели", many = "недель", ), "ru")

  override val weeks_future: VoidPluralString = VoidPluralString(PluralForms(one = "неделю", ),
      "ru")

  override val weeks_past: VoidPluralString = VoidPluralString(PluralForms(one = "неделю", ), "ru")

  override val months: VoidPluralString = VoidPluralString(PluralForms(one = "месяц", few =
      "месяца", many = "месяцев", ), "ru")

  override val months_future: VoidPluralString? = null

  override val months_past: VoidPluralString? = null

  override val years: VoidPluralString = VoidPluralString(PluralForms(one = "год", few = "года",
      many = "лет", ), "ru")

  override val years_future: VoidPluralString? = null

  override val years_past: VoidPluralString? = null
}
